<template>
  <section class="product">
    <TheTitle :theme="theme" :title="title" />
      
    <div class="container wrapper">
        <!-- image gallery -->
        <div class="product-image-gallery" >
            <div class="main-image">
                <img :src="mainImage" alt="Product image">
            </div>
            <div class="other-images">
                <img
                  v-show="imageArrayLength"
                  v-for="img in product_data.imgs"
                  :key="img"
                  :src="`http://old.phoenixglobaltradings.com/images/${img.img}`"
                  @click="changeMainImage(img.img)"
                  alt="other-images"
                >
            </div>
        </div>
        <!-- product info -->
        <div class="product-info">
            <h2 class="product-name">{{ product_data.name }}</h2>
            <p class="subtitle">{{ product_data.subtitle }}</p>
            <p class="description">{{ product_data.info }}</p>
            
            <!-- TABLE -->
            <div class="table">
                <div v-html="product_data.table"></div>
            </div>
        </div>
    </div>
  </section>
</template>

<script>
import TheTitle from "../components/The-title";
// import ProductCard from "../components/Product-card";
import axios from "axios";

export default {
  name: "Product",
  components: {
    TheTitle
  },
  data() {
    return {
      theme: "Main menu / Product ",
      title: "Product Info",
      mainImage: ``,
      productId: this.$route.params.productId,
      product_data: {}
    };
  },
  computed: {
    imageArrayLength() {
      return this.product_data.imgs.length > 1 ?  true :  false
    }
  },
  methods: {
    changeMainImage(img) {
      this.mainImage = `http://old.phoenixglobaltradings.com/images/${img}`
    },
    async getProduct() {
      const dataRes = await axios(` http://old.phoenixglobaltradings.com/product/${this.productId}`).then(res => res.data)
      this.product_data = dataRes
      this.mainImage = `http://old.phoenixglobaltradings.com/images/${this.product_data.imgs[0].img}`
    }
  },
  mounted() {
    this.getProduct()
  },
};
</script>

<style lang="scss">
@mixin media-size($size) {
  @media (max-width: $size) {
    @content;
  }
}
@mixin min-width($size) {
  @media (min-width: $size) {
    @content;
  }
}

.product {
  min-height: 100vh;
  padding: 220px 0 70px;
  // Media
  @include media-size(1230px) {
    padding: 170px 0 30px;
  }
  @include media-size(790px) {
    padding: 150px 0 30px;
  }
  @include media-size(570px) {
    padding: 150px 15px 30px;
  }

  .wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 100px;
    // Media
    @include media-size(790px) {
      padding-top: 50px;
    }

    .product-image-gallery {
      width: 40%;
      // Media
      @include media-size(790px) {
        width: 100%;
      }

      .main-image {
        width: 100%;
        border: 10px solid #fff;
        padding: 10px;
        background: #fff;

        img {
          width: 100%;
          object-fit: cover;
        }
      }

      .other-images {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;

        img {
          width: calc(100% / 3);
          height: 200px;
          object-fit: cover;
          border: 10px solid #fff;
          cursor: pointer;
          // Media
          @include media-size(1230px) {
            height: 150px;
          }
          @include media-size(1020px) {
            height: 100px;
          }
          @include media-size(790px) {
            height: 200px;
          }
          @include media-size(570px) {
            width: 50%;
          }
          @include media-size(400px) {
            height: 150px;
          }
          @include min-width(800px) {
            opacity: 0.8;
          }

          &:hover {
            @include min-width(800px) {
              opacity: 1;
            }
          }
        }
      }
    }

    .product-info {
      width: 50%;
      // Media
      @include media-size(790px) {
        width: 100%;
        margin-top: 50px;
      }

      .product-name {
        font-size: 40px;
        margin-bottom: 40px;
        // Media
        @include media-size(1230px) {
          font-size: 30px;
          margin-bottom: 25px;
        }
        @include media-size(1020px) {
          
        }
      }
      
      .subtitle{
        font-weight: 700;
        font-size: 22px;
        color: #777;
        margin-bottom: 15px;
      }

      .description {
        font-size: 20px;
      }

      .table {
        margin: 50px 0;
        width: 100%;
        // Media
        @include media-size(1020px) {
          margin: 25px 0;
        }

        ul {
          width: 100%;

          li {
            width: 100%;
            display: flex;
            justify-content: space-between;
            background: rgba(185, 116, 59, 0.2);
            &:nth-child(2n) {
              background: #fff;
            }
            &:hover {
              @include min-width(1000px) {
                background: #1d69bf2a;
              }
            }

            p {
              width: 50%;
              text-align: left;
              font-size: 18px;
              font-weight: 700;
              padding: 10px;
              // Media
              @include media-size(1020px) {
                font-size: 15px;
              }
              @include media-size(570px) {
                padding: 7px;
              }

              &.key {
                color: #555;
              }
              &.value {
                color: #ba5400;
              }
            }
          }
        }
      }
    }
  }
}
</style>